<template>
	<div>
		<div class="item" @click="goEach('所需证件列表')">
			<i></i>
			<span>所需证件列表</span>
			<span class="undone"></span>
		</div>
		<div class="item" @click="uploading()">
			<i></i>
			<span>上传证件</span>
			<span class="undone"></span>
		</div>
		<div class="item" @click="goEach('查看已上传证件照')">
			<i></i>
			<span>查看已上传证件照</span>
			<span class="undone">10</span>
		</div>
		<div class="item" @click="goEach('查看模板')">
			<i></i>
			<span>查看模板</span>
			<span class="undone"></span>
		</div>
		<div class="intro">
			<h3>证件上传说明</h3>
			<p>1. 所有扫描件均为原件扫描件；</p>
			<p>2. 所有扫描件须去掉外壳；</p>
			<p>3. 所有扫描件均须扫描完整（即四边形、四个角均在扫描范围内）；</p>
			<p>4. 所有扫描件均须清晰；</p>
			<p>5. 所有扫描件均须彩色扫描；</p>
			<p>6. 扫描请生成JPG格式，并存入手机相册，每张不可超过5M。</p>
		</div>
		
		<div  class="alert alert-msg" v-if="uploadshow">
			<div class="box">
				<div  class="txt" ref="msgTxt">{{upload}}</div>
			</div>
		</div>
		
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				uploadshow:false,
				upload:'正在上传中，请勿离开本页面（4/10）'
			}
		},
		methods:{
			goEach(text){
				if(text=='所需证件列表'){
					this.$router.push({name: 'Documentslist'})
				}else if(text=='查看已上传证件照'){
					this.$router.push({name: 'Photouploaded'})
				}else if(text=='查看模板'){
					this.$router.push({name: 'Checktemplate'})
				}
			},
			uploading(){
				
				let str1="正在上传中，请勿离开本页面（4/10）";
				let str2="上传中断，请监测网络是否正常";
				let str3="上传完毕！";
				
				setTimeout(() => { 
		            this.uploadshow=true;
					this.upload=str1
		        }, 1000),
		        setTimeout(() => { 
		            this.upload=str2;
		        }, 4000),
		        setTimeout(() => { 
		            this.upload=str3;
		        }, 7000),
		        setTimeout(() => { 
		            this.uploadshow=false;
		        }, 9000)
				
			}
		}
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	
	.item{width: 100%;height: 0.9rem;border-bottom: 0.01rem solid #e8e8e8;position: relative;line-height: 0.9rem;font-size: 0.3rem;padding: 0 0.3rem;box-sizing: border-box;
		i{width: 0.13rem;height: 0.23rem;background: url(../../assets/go.png) no-repeat;background-size:100% 100% ;position: absolute;right: 0.3rem;top: 38%;}
		.complete{color: #333333;float: right;margin-right: 0.3rem;}
		.undone{color: #fa8a12;float: right;margin-right: 0.3rem;}
	}
	.intro{padding: .3rem; font-size: .28rem; line-height: .44rem; color: #666666;
		h3{font-size: .3rem; color: #1a1a1a; line-height: .7rem; margin-bottom: .1rem;}
	}
	
	.alert-msg { background-color: rgba(255, 255, 255, 0.3);
		.box{width: 100%; height: 1.5rem; margin-top: -3.5rem; left: 0; margin-left: 0; text-align: center;}
		.txt { background-color: rgba(0, 0, 0, 0.7); color: #fff; display: inline-block; line-height: .8rem; border-radius: .1rem; min-width: 3rem; text-align: center; font-size: .3rem; padding: 0 .3rem; }
	}
	
</style>


